import css from './styles.scss';


if (typeof window !== 'undefined') {
  require('particles.js')
}

export default class ParticlesJS extends React.Component {
  componentDidMount() {
    particlesJS('particles-js', { // eslint-disable-line no-undef
      particles: {
        number: {
          value: 50
        },
        color: {
          value: '#313131'
        },
        opacity: {
          value: 1,
          random: true,
          anim: {
            enable: true,
            speed: 0.2,
            opacity_min: 0.1,
            sync: false
          }
        },
        size: {
          value: 3,
          random: true,
          anim: {
            enable: false
          }
        },
        line_linked: {
          enable: false,
          color: '#000'
        }
      },
      interactivity: {
        detect_on: 'canvas',
        events: {
          onhover: {
            enable: true,
            mode: 'grab'
          },
          onclick: {
            enable: false
          },
          resize: true
        },
        modes: {
          grab: {
            distance: 100,
            line_linked: {
              opacity: 0.5
            }
          }
        }
      },
      retina_detect: true
    });
  }

  render() {
    return (
      <div id="particles-js" className={css.particles} />
    );
  }
}
